            wiggle2={wiggle2}
            setTurnSwap={setTurnSwap}
            count={count}
            bombPositionC1={bombPositionC1}
            bombPositionC2={bombPositionC2}
            cycleBombPositionC1={cycleBombPositionC1}
            cycleBombPositionC2={cycleBombPositionC2}
